@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500);


*, ::before, ::after {
margin: 0;
padding: 0;
box-sizing: border-box;
}
.grid_container{
  min-height:100vh ;
}
:root {
  /* colors */
  --body-bg-colors: #FFFFFF;
  --sidebar-bg-colors: #f4f4f4;
  --text-color: #333333;
  --title-color: #111111;
  --date-color: #1f94c2;

 

  --icon-color:var(--text-color);
  --circle-color:#ffffff;
  --circle-color-plain : #0e0d0d;
  --icon-bg-color : #FFFFFF;
  --dark-body-bg-color: #242424;
  --dark-sidebar-bg-color: #171717;
  --dark-text-color: #d8d8d8;
  --dark-title-color: #ffffff;
  --dark-date-color: #e6ef71;
  --dark-icon-color:var(--dark-text-color);
  --dark-circle-color: rgb(108, 108, 108);
  --dark-circle-color-plain: rgb(238, 238, 238);


  /* fonts */
  --boby-font: "Poppins","Sans-serif";
  --h1-font-size: 1.7rem;
  --h2-font-size: 1.3rem;
  --h3-font-size: 1.1rem;
  --h4-font-size: 1rem;
  
  /* helpers*/

  --spacing-1: .4rem;
  --spacing-2: .8rem;
  --spacing-3: .1.2rem;
  --spacing-4: 1.5em;
  --spacing-5: 2em;

}

body{
  font-family: "Poppins","Sans-serif";
  font-family: var(--boby-font);
  font-weight: 200;
  color: #333333;
  color: var(--text-color);
  background-color: #FFFFFF;
  background-color: var(--body-bg-colors);
}
.dark {  
  background-color: #242424;  
  background-color: var(--dark-body-bg-color);
  color: #d8d8d8;
  color: var(--dark-text-color);
}

h1,h2,h3{
  margin-bottom: .4rem;
  margin-bottom: var(--spacing-1);
  font-weight: 400;
  color: #111111;
  color: var(--title-color);
}
.dark h1, .dark h2, .dark h3 {
  color: #ffffff;
  color: var(--dark-title-color);
}

h2{
  display: flex;
  align-items: center;
  margin-bottom: 1.5em;
  margin-bottom: var(--spacing-4);
  font-size: 1.3rem;
  font-size: var(--h2-font-size);
  letter-spacing: 4px;
  text-transform: uppercase;
}

h1{
  font-size: 1.7rem;
  font-size: var(--h1-font-size);
  text-transform: uppercase;
}

a{
  text-decoration: none;
  color: inherit;
}
.dark a {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

p{
  margin-bottom: .8rem;
  margin-bottom: var(--spacing-2);
}

ul{
  padding-left: 1.3rem;
} 
li{
  margin-bottom: .8rem;
  margin-bottom: var(--spacing-2);
}



button {
  background: none;
  border:none;
  outline: none;
  cursor: pointer;
}

.dark button {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

/**/
.mb3{
  margin-bottom: .1.2rem;
  margin-bottom: var(--spacing-3);
}
.mb5{
  margin-bottom: 2em;
  margin-bottom: var(--spacing-5);
}

/* grid_container */

@media screen and (min-width:768px) {
  
  .grid_container{
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
  .grid__row{
    display: grid;
    grid-template-columns: 1fr 2fr;
  }

}
/* layouts */
.sidebar{
  padding: 1.5rem;
  background-color: #f4f4f4;
  background-color: var(--sidebar-bg-colors);
}
.action{
  display: flex;
  justify-content: space-between;
}

/* Icons */
.circle {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #ffffff;
  background-color: var(--circle-color);
}

.circle-plain {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #0e0d0d;
  background-color: var(--circle-color-plain);
}
.dark .circle {
  background: rgb(108, 108, 108);
  background: var(--dark-circle-color);
}
.dark .circle-plain {
  background: rgb(238, 238, 238);
  background: var(--dark-circle-color-plain);
}


.main{
  padding: 1.5rem;
}
.dark .sidebar {
  background-color: #171717;
  background-color: var(--dark-sidebar-bg-color);
}


/* Generate PDF */

#cv-print {
  width: 100% !important;
} 
.cv-print ul {
  margin:0;
  padding:0;
  
}
.cv-print ul li {
  list-style-type:none;
}
.cv-print ul li:before {
  content: "â€¢";
  display: inline-block; 
  font-size: 110%;
  padding-right: 10px;
}
.cv-print .grid__container {
  font-size:12px !important;
  max-width: 210mm;
}
.cv-print .sidebar {
  height: 297mm;
}
.cv-print .user__infos svg {
  display: none !important;
}
.cv-print .circle {
  width:10px;
  height:10px;
}
.cv-print .grid__date {
  margin-top:5px;
}
.user{
    text-align: center;
}
.user__avatar{
    border-radius: 25%;
}
.user__infos{
    text-align: left;
    padding:var(--spacing-3)0 var(--spacing-1)0;
}
.user__info{
    display: flex;
}
.user__info svg{
    color:var(--icon-color);
    margin-right: 1rem;
}

.skills{
    margin: var(--spacing-3)0;
}

.skill{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.grid__item,
.grid__item p {    
  margin-bottom: var(--spacing-1)
}
.grid__date {  
  font-weight: 500;
}
.grid__title {
  margin-bottom: 0;
}
.grid__location {
  font-weight: 300;
  color:var(--date-color);
}
.dark .grid__location {
  
  color: var(--dark-date-color);
}
.grid__mission {
  margin-bottom:var(--spacing-1);
}
